import React from "react";
import { featureBreakdown } from "./pricing-frame-data";
import Tooltip from "rc-tooltip";
import "rc-tooltip/assets/bootstrap.css";

const itemBox = {
  padding: "20px",
  marginBottom: "15px",
  borderRadius: "10px",
  background: "#fafafa",
  border: "1px solid #eee",
};

const titleStyle = {
  fontSize: "16px",
  fontWeight: 700,
  marginBottom: "6px",
};

const descStyle = {
  fontSize: "14px",
  marginBottom: "4px",
};

const PricingFeatureBreakdown = ({ lang }: { lang: "en" | "vn" }) => {
  return (
    <div style={{ marginTop: 50 }}>
      <h2 style={{ marginBottom: 20 }}>
        {lang === "en" ? "Feature Breakdown" : "M t tng hng mc"}
      </h2>

      {featureBreakdown.map((f, i) => (
        <div key={i} style={itemBox}>
          <div style={titleStyle}>{f.title[lang]}</div>
          <div style={descStyle}>{f.desc[lang]}</div>
          <div style={{ fontSize: 13, opacity: 0.8 }}>
            {f.whyBetter[lang]}
          </div>
        </div>
      ))}
    </div>
  );
};

export default PricingFeatureBreakdown;
