// src/components/cms/PricingFrame.tsx
import React, { useState } from "react";
import Tooltip from "rc-tooltip";
import "rc-tooltip/assets/bootstrap.css";

import { pricingData } from "./pricing-frame-data";
import PricingRebuildCosts from "./PricingRebuildCosts";
import PricingGrowthTiers from "./PricingGrowthTiers";
import PricingFeatureBreakdown from "./PricingFeatureBreakdown";

const th = {
  padding: "12px 10px",
  fontWeight: 600,
  fontSize: "14px",
  borderRight: "1px solid #eee",
  height: 48,
  overflow: "hidden",
  whiteSpace: "nowrap",
  textOverflow: "ellipsis",
  lineHeight: "48px",
  userSelect: "none",
};

const td = {
  padding: "10px 10px",
  fontSize: "14px",
  borderRight: "1px solid #eee",
  verticalAlign: "middle",
  height: 48,
  overflow: "hidden",
  whiteSpace: "nowrap",
  textOverflow: "ellipsis",
  lineHeight: "48px",
  userSelect: "none",
};

export const PricingFrame = () => {
  const [lang, setLang] = useState<"en" | "vn">("en");

  const wrap = (value: string | undefined) => (
    <Tooltip placement="top" overlay={value} destroyTooltipOnHide>
      <span style={{ cursor: "default" }}>{value}</span>
    </Tooltip>
  );

  return (
    <div style={{ padding: "40px" }}>
      {/* GLOBAL LANGUAGE TOGGLE */}
      <div style={{ marginBottom: "30px" }}>
        <button
          onClick={() => setLang("en")}
          style={{
            marginRight: "10px",
            padding: "6px 14px",
            cursor: "pointer",
            background: lang === "en" ? "#000" : "#eee",
            color: lang === "en" ? "#fff" : "#000",
            borderRadius: "6px",
          }}
        >
          EN
        </button>

        <button
          onClick={() => setLang("vn")}
          style={{
            padding: "6px 14px",
            cursor: "pointer",
            background: lang === "vn" ? "#000" : "#eee",
            color: lang === "vn" ? "#fff" : "#000",
            borderRadius: "6px",
          }}
        >
          VN
        </button>
      </div>
    
      
      {/* TABLE 3  GROWTH TIERS */}
      <PricingGrowthTiers lang={lang} />
      <br/>      <br/>      <br/>

      {/* TABLE 1  MAIN COMPARISON */}
      <h2 style={{ marginBottom: 20 }}>
        {lang === "en" ? "Competitor Pricing & Usage Comparison" : "So snh gi dch v"}
      </h2>

      <div
        style={{
          overflowX: "auto",
          borderRadius: "12px",
          boxShadow: "0 1px 4px rgba(0,0,0,0.12)",
          marginBottom: 60,
        }}
      >
        <table
          style={{
            width: "100%",
            borderCollapse: "collapse",
            minWidth: "950px",
            tableLayout: "fixed",
          }}
        >
          <thead>
            <tr style={{ background: "#f8f9fa" }}>
              <th style={th}>{wrap(lang === "en" ? "Service" : "Hng mc")}</th>
              <th style={th}>{wrap("Slap Kard LLC")}</th>
              <th style={th}>{wrap("Viettel Cloud (VPS)")}</th>
              <th style={th}>{wrap("Bizfly Cloud")}</th>
              <th style={th}>{wrap("Haravan / Sapo")}</th>
              <th style={th}>{wrap(lang === "en" ? "Notes" : "Ghi ch")}</th>
            </tr>
          </thead>

          <tbody>
            {pricingData.map((row, i) => (
              <tr
                key={i}
                style={{
                  background: i % 2 === 0 ? "#fff" : "#fafafa",
                  borderBottom: "1px solid #eee",
                }}
              >
                <td style={td}>{wrap(row.service[lang])}</td>
                <td style={td}>{wrap(row.you[lang])}</td>
                <td style={td}>{wrap(row.c1[lang])}</td>
                <td style={td}>{wrap(row.c2[lang])}</td>
                <td style={td}>{wrap(row.c3[lang])}</td>
                <td style={td}>{wrap(row.notes[lang])}</td>
              </tr>
            ))}

            {/* TOTAL ROW */}
            <tr style={{ background: "#eef6ff", fontWeight: "bold" }}>
              <td style={td}>
                {wrap(
                  lang === "en"
                    ? "Total Cost (Equivalent)"
                    : "Tng gi tr tng ng"
                )}
              </td>

              <td style={td}>{wrap("1,500,000 VND/mo")}</td>
              <td style={td}>{wrap("3M  6M/mo")}</td>
              <td style={td}>{wrap("2M  5M/mo")}</td>
              <td style={td}>{wrap("3M  10M/mo")}</td>
              <td style={td}>
                {wrap(
                  lang === "en"
                    ? "Our price is significantly lower."
                    : "Gi bn a ra thp hn rt nhiu."
                )}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      {/* TABLE 2  REBUILD COSTS */}
      <PricingRebuildCosts lang={lang} />


      <PricingFeatureBreakdown lang={lang} />

    </div>
  );
};

export default PricingFrame;
