// src/components/cms/PricingRebuildCosts.tsx
import React from "react";
import Tooltip from "rc-tooltip";
import "rc-tooltip/assets/bootstrap.css";

import { rebuildCosts } from "./pricing-frame-data";

const th: React.CSSProperties = {
  padding: "12px 10px",
  fontWeight: 600,
  fontSize: "14px",
  borderRight: "1px solid #eee",
  height: 48,
  overflow: "hidden",
  whiteSpace: "nowrap",
  textOverflow: "ellipsis",
  lineHeight: "48px",
  userSelect: "none",
};

const td: React.CSSProperties = {
  padding: "10px 10px",
  fontSize: "14px",
  borderRight: "1px solid #eee",
  verticalAlign: "middle",
  height: 48,
  overflow: "hidden",
  whiteSpace: "nowrap",
  textOverflow: "ellipsis",
  lineHeight: "48px",
  userSelect: "none",
};

const PricingRebuildCosts = ({ lang }: { lang: "en" | "vn" }) => {
  const wrap = (val: string | undefined) => (
    <Tooltip placement="top" overlay={val} destroyTooltipOnHide>
      <span style={{ cursor: "default" }}>{val}</span>
    </Tooltip>
  );

  return (
    <div style={{ marginTop: 50 }}>
      <h2 style={{ marginBottom: 20 }}>
        {lang === "en"
          ? "Cost to Rebuild This System on Other Platforms"
          : "Chi ph  ti to h thng ny trn nn tng khc"}
      </h2>

      <div
        style={{
          overflowX: "auto",
          borderRadius: "12px",
          boxShadow: "0 1px 4px rgba(0,0,0,0.12)",
        }}
      >
        <table
          style={{
            width: "100%",
            borderCollapse: "collapse",
            minWidth: "900px",
            tableLayout: "fixed",
          }}
        >
          <thead>
            <tr style={{ background: "#f8f9fa" }}>
              <th style={th}>{wrap(lang === "en" ? "Service" : "Hng mc")}</th>
              <th style={th}>{wrap(lang === "en" ? "Your System" : "Gii php ca bn")}</th>
              <th style={th}>{wrap("Viettel Cloud")}</th>
              <th style={th}>{wrap("Bizfly Cloud")}</th>
              <th style={th}>{wrap("AWS")}</th>
            </tr>
          </thead>

          <tbody>
            {/* ROWS */}
            {rebuildCosts.rows.map((row, i) => (
              <tr
                key={i}
                style={{
                  background: i % 2 === 0 ? "#fff" : "#fafafa",
                  borderBottom: "1px solid #eee",
                }}
              >
                <td style={td}>{wrap(row.item[lang])}</td>
                <td style={td}>{wrap(row.you[lang])}</td>
                <td style={td}>{wrap(row.viettel[lang])}</td>
                <td style={td}>{wrap(row.bizfly[lang])}</td>
                <td style={td}>{wrap(row.aws[lang])}</td>
              </tr>
            ))}

            {/* TOTAL ROW */}
            <tr style={{ background: "#eef6ff", fontWeight: "bold" }}>
              <td style={td}>
                {wrap(lang === "en" ? "Total Monthly Cost" : "Tng chi ph hng thng")}
              </td>
              <td style={td}>{wrap(rebuildCosts.totals.you[lang])}</td>
              <td style={td}>{wrap(rebuildCosts.totals.viettel[lang])}</td>
              <td style={td}>{wrap(rebuildCosts.totals.bizfly[lang])}</td>
              <td style={td}>{wrap(rebuildCosts.totals.aws[lang])}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default PricingRebuildCosts;
