import React from "react";
import Tooltip from "rc-tooltip";
import "rc-tooltip/assets/bootstrap.css";

import { growthTiers } from "./pricing-frame-data";

const th: React.CSSProperties = {
  padding: "12px 10px",
  fontWeight: 600,
  fontSize: "14px",
  borderRight: "1px solid #eee",
  height: 48,
  overflow: "hidden",
  whiteSpace: "nowrap",
  textOverflow: "ellipsis",
  lineHeight: "48px",
  userSelect: "none",
};

const td: React.CSSProperties = {
  padding: "10px 10px",
  fontSize: "14px",
  borderRight: "1px solid #eee",
  verticalAlign: "middle",
  height: 48,
  overflow: "hidden",
  textOverflow: "ellipsis",
  whiteSpace: "nowrap",
  lineHeight: "48px",
  userSelect: "none",
};

const PricingGrowthTiers = ({ lang }: { lang: "en" | "vn" }) => {
  const wrap = (val: string | undefined) => (
    <Tooltip placement="top" overlay={val} destroyTooltipOnHide>
      <span style={{ cursor: "default" }}>{val}</span>
    </Tooltip>
  );

  return (
    <div style={{ marginTop: 50 }}>
      <h2 style={{ marginBottom: 20 }}>
        {lang === "en"
          ? "Slap Kard's Scaling Costs by Growth Tier"
          : "Slap Kard Chi ph theo mc tng trng"}
      </h2>

      <div
        style={{
          overflowX: "auto",
          borderRadius: "12px",
          boxShadow: "0 1px 4px rgba(0,0,0,0.12)",
        }}
      >
        <table
          style={{
            width: "100%",
            borderCollapse: "collapse",
            minWidth: "900px",
            tableLayout: "fixed",
          }}
        >
          <thead>
            <tr style={{ background: "#f8f9fa" }}>
              <th style={th}>{wrap(lang === "en" ? "Metric" : "Ch s")}</th>
              <th style={th}>{wrap(lang === "en" ? "Your System" : "Gii php ca bn")}</th>
              <th style={th}>{wrap(lang === "en" ? "Tier 1" : "Mc 1")}</th>
              <th style={th}>{wrap(lang === "en" ? "Tier 2" : "Mc 2")}</th>
              <th style={th}>{wrap(lang === "en" ? "Tier 3" : "Mc 3")}</th>
            </tr>
          </thead>

          <tbody>
            {growthTiers.map((row, i) => (
              <tr
                key={i}
                style={{
                  background: i % 2 === 0 ? "#fff" : "#fafafa",
                  borderBottom: "1px solid #eee",
                }}
              >
                <td style={td}>{wrap(row.metric[lang])}</td>
                <td style={td}>{wrap(row.you[lang])}</td>
                <td style={td}>{wrap(row.t1[lang])}</td>
                <td style={td}>{wrap(row.t2[lang])}</td>
                <td style={td}>{wrap(row.t3[lang])}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default PricingGrowthTiers;
